export default {
	onLoad() {
		var that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.users = res.data;
				console.log(that.users);
				that.improveFriend();
			}
		});
	},
	data() {
		return {
			users:{},
			friends: []
		};
	},
	methods:{
		improveFriend: function() {
			uni.request({
				url: this.$baseUrl + '/friend/application',
				method: 'POST',
				data: {
					toId: this.users.id
				},
				success: res => {
					if (res.data.code === 1) {
						console.log(res.data.data)
						this.friends = res.data.data;
					} else {
						uni.showToast({
							title: res.data.msg
						});
					}
				}
			});
		},
		agree:function(fromId, toId){
			uni.request({
				url:this.$baseUrl+'/friend/confirm',
				method:'PUT',
				data:{
					fromId: this.users.id,
					toId: toId
				},
				success:res =>{
					if(res.data.code === 1){
						uni.showToast({
							title:res.data.msg
						})
					}else{
						uni.showToast({
							title:res.data.msg
						})
					}
				}
			})
		},
		reject:function(fromId, toId){
			uni.request({
				url:this.$baseUrl+'/friend/reject',
				method:'DELETE',
				data:{
					fromId:fromId,
					toId:toId
				},
				success: res =>{
				if(res.data.code === 1){
					uni.showToast({
						title:res.data.msg
					})
				}else{
					uni.showToast({
						title:res.data.msg
					})
				}
				}
			})
		}
	}
	
};
